                >
                  {{ translate.type[opt] }}
                </option>
                <option
                  v-if="cardData.type === 'monster' && cardData.type3 !== 'lb'"
                  value="lj"
                >
                  {{ translate.type['lj'] }}
                </option>
                <option
                  v-show="cardData.type !== 'monster'"
