                </option>
              </select>
            </div>
          </section>

          <label v-if="cardData.type2 === 'lj'">链接箭头</label>
          <arrow v-if="cardData.type2 === 'lj'" v-model="cardData.link" />

          <label v-if="cardData.type === 'monster'">属性</label>
