          />
          <datalist id="race">
            <option
              v-for="race in config.translate.raceList"
              :key="race"
              :value="race"
            ></option>
          </datalist>
          <label v-if="cardData.type === 'monster'">攻击 / 防御</label>
          <section v-if="cardData.type === 'monster'" class="num2">
            <input type="text" placeholder="ATK" v-model="cardData.attack" />
            <input type="text" placeholder="DEF" v-model="cardData.defend" />
          </section>

          <label v-if="cardData.type3 === 'lb'">灵摆刻度</label>
          <input
            v-if="cardData.type3 === 'lb'"
            type="number"
            v-model="cardData.lb_num"
          />

          <label v-if="cardData.type3 === 'lb'">灵摆效果</label>
          <textarea
            v-if="cardData.type3 === 'lb'"
            v-model="cardData.lb_desc"
          ></textarea>

          <label>效果</label>
          <textarea v-model="cardData.desc"></textarea>
          
          <label>版权文字</label>
          <input
            v-model="cardData.copyright"
            placehodel="请输入版权文字"
          />

          <label>偏好</label>
          <section2>
            <Turn v-model="cardbagSwitch" label="卡包"></Turn>
            <Turn v-model="passwordSwitch" label="卡密"></Turn>
            <Turn v-model="holo" label="防伪标志"></Turn>
          </section2>

          <label v-if="cardbagSwitch">卡包</label>
          <input
            v-if="cardbagSwitch"
            v-model="cardData.cardbag"
            placehodel="请输入卡包编号"
          />

          <button @click="download">制作完毕</button>
        </form>
      </div>

      <Loading ref="_load_">正在加载</Loading>
    </div>
  </div>
</template>

<script>
import Router from "vue-router";
import Card_ from "./Card";
import { Card, configs } from "C:/Users/ymssx/Desktop/cardjs/source/card.js";
import Loading from "./Loading";
import { Cloud } from "../../../ygodiy/src/lib/ajax";
import Cut from "./Cut";
import Turn from "./Turn";
import ColorSelector from "./ColorSelector";
import arrow from "./arrow";
import AttributeSelector from './AttributeSelector';

const { cn, cnSimplify, jp, jpNotation, en } = configs;

const cnIfm = {
  name: "浮幽櫻",
  _id: "62015408",
  type: "monster",
  type2: "xg",
  type3: "",
  type4: "",
  attack: 0,
  defend: 1800,
  level: 3,
  cardbag: "SD38-JP001",
  desc:
    "這個卡名的效果1回合只能使用1次。①：對方場上的怪獸數量比自己場上的怪獸多的場合，把這張卡從手卡丟棄才能發動。選自己的額外卡組1張卡給雙方確認。那之後，把對方的額外卡組確認，有選的卡的同名卡的場合，那些對方的同名卡全部除外。這個效果在對方回合也能發動。",
  race: "不死族",
  link: [0, 0, 0, 0, 0, 0, 0, 0],
  attribute: "dark",
  copyright: "ⓒ高桥和希 スタジオ・ダイス/集英社",
};

const cnSimplifyIfm = {
  name: "浮幽樱",
  _id: "62015408",
  type: "monster",
  type2: "xg",
  type3: "",
  type4: "",
  attack: 0,
  defend: 1800,
  level: 3,
  cardbag: "SD38-JP001",
  desc:
    "这个卡名的效果1回合只能使用1次。①：对方场上的怪兽数量比自己场上的怪兽多的场合，把这张卡从手卡丢弃才能发动。选自己的额外卡组1张卡给双方确认。那之后，把对方的额外卡组确认，有选的卡的同名卡的场合，那些对方的同名卡全部除外。这个效果在对方回合也能发动。",
  race: "不死族",
  link: [0, 0, 0, 0, 0, 0, 0, 0],
  attribute: "dark",
  copyright: "ⓒ2020 Studio Dice/SHUEISHA, TV TOKYO,KONAMI",
};

const jpIfm = {
  // name: '苦澀的抉擇',
  name: "浮幽さくら",
  //name: 'Rainbow Dragon',
  _id: "62015408",
  type: "monster",
  type2: "xg",
  type3: "",
  type4: "",
  attack: 0,
  defend: 1800,
  level: 3,
  cardbag: "SD38-JP001",
  desc:
    "このカード名の効果は１ターンに１度しか使用できない。①：相手フィールドのモンスターの数が自分フィールドのモンスターより多い場合、このカードを手札から捨てて発動できる。自分のＥＸデッキのカード１枚を選んでお互いに確認する。その後、相手のＥＸデッキを確認し、選んだカードの同名カードがある場合、その相手の同名カードを全て除外する。この効果は相手ターンでも発動できる。",
  //desc: '全身都被藍寶石覆蓋著，异常美麗的龍。雖然不喜歡競爭。但卻具備著非常高的攻擊力。',
  //desc: 'hello  world, this is a YU-Gi-Oh card maker',
  race: "不死族",
  link: [0, 0, 0, 0, 0, 0, 0, 0],
  attribute: "dark",
  copyright: "ⓒ高桥和希 スタジオ・ダイス/集英社",
};

const enIfm = {
  name: "Ghost Reaper & Winter Cherries",
  _id: "62015408",
  type: "monster",
  type2: "xg",
  type3: "tz",
  type4: "",
  attack: 0,
  defend: 1800,
  level: 3,
  cardbag: "SD38-JP001",
  desc:
    "The effect of this card name can only be used once in a round.① : when the number of monsters on the opponent's field is more than that on your own field, discard this card from your hand card to start. Choose your own extra card group 1 card to both sides for confirmation. After that, confirm the additional card group of the other party. If there is a selected card with the same name, all the cards with the same name are excluded. This effect can also be activated in the opponent's turn.",
  race: "Zombie",
  link: [0, 0, 0, 0, 0, 0, 0, 0],
  attribute: "dark",
  copyright: "ⓒ1996 KAZUKI TAKAHASHI",
};

var uploader = null;

export default {
  name: "CardMake",
  components: { ColorSelector, Turn, Cut, Loading, Card: Card_, arrow, AttributeSelector },
  props: { event },
  data() {
    return {
      cardData: { ...cnIfm },
      config: cn,
      translate: cnSimplify.translate,
      cardIns: null,
      lang: "cn",
      state: false,
      uploadImgSrc: null,
      search: false,
      liSelected: 0,
      asideState: false,
      holo: true,
      passwordSwitch: true,
      cardbagSwitch: false,
      type2_list: ["tc", "xg", "rh", "ys", "tt", "cl", "tk"],
      type3_list: ["", "tc", "tz", "ec", "lh", "fz", "ts", "tm", "kt"],
      type_mg_list: ["tc", "yx", "zb", "ys", "cd", "fj", "sg"],
    };
  },
  watch: {
    lang() {
      switch (this.lang) {
        case "cn":
          this.cardIns.lang = "cn";
          this.changeConfig(cn, { ...cnIfm });
          break;
        case "cnSimplify":
          this.cardIns.lang = "cn";
          this.changeConfig(cnSimplify, { ...cnSimplifyIfm });
          break;
        case "jp":
          this.cardIns.lang = "jp";
          this.changeConfig(jp, { ...jpIfm });
          break;
        case "jpNotation":
          this.cardIns.lang = "jp";
          this.changeConfig(jpNotation, { ...jpIfm });
          break;
        case "en":
          this.cardIns.lang = "en";
          this.changeConfig(en, { ...enIfm });
          break;
        default:
          this.cardIns.lang = "cn";
          this.changeConfig(cn, { ...cnIfm });
          break;
      }
    },
    holo() {
      if (!this.cardIns) {
        return;
      }
      this.cardIns.holo = this.holo;
      this.cardIns.render();
    },
    passwordSwitch() {
      if (!this.cardIns) {
        return;
      }
      this.cardIns.passwordSwitch = this.passwordSwitch;
      this.cardIns.render();
    },
    cardbagSwitch() {
      if (!this.cardIns) {
        return;
      }
      this.cardIns.cardbagSwitch = this.cardbagSwitch;
      this.cardIns.render();
    },
  },
  methods: {
    cardInsLoaded(cardIns) {
      this.cardIns = cardIns;
      this.$refs._load_.hideThis();
    },
    showAlert(msg) {
      alert(msg);
    },
    gotoOcgWiki() {
      window.open("http://ocg.wiki/#" + this.cardData._id);
    },
    download() {
      if (!this.cardIns) {
        return;
      }
      this.cardIns.save();
    },
    showForm() {
      if (!this.state) {
        this.state = true;
      }
    },
    hideForm() {
      if (this.state) {
        this.state = false;
      }
    },
    changeConfig(config, cardData) {
      if (!this.cardIns) {
        return;
      }
      this.config = config;
      this.cardData = cardData;
      this.cardIns.changeConfig(config);
      this.cardIns.feedData(cardData);
    },
    uploadPic(e) {
      uploader = e.target;

      let img = uploader.files[0];

      function getObjectURL(file) {
        var url = null;
        if (window.createObjectURL != undefined) {
          // basic
          url = window.createObjectURL(file);
        } else if (window.URL != undefined) {
          // mozilla(firefox)
          url = window.URL.createObjectURL(file);
        } else if (window.webkitURL != undefined) {
          // webkit or chrome
          url = window.webkitURL.createObjectURL(file);
        }
        return url;
      }

      this.uploadImgSrc = getObjectURL(img);

      let image = new Image();
      image.src = this.uploadImgSrc;
      image.onload = () => {
        this.cardIns.feed(image, img);
      };
    },
    clearUpload() {
      this.uploadImgSrc = null;

      if (uploader) {
        uploader.value = null;
      }
    },
    raceChange(e) {
      if (e.target.value.substr(-1) !== "族" && this.lang !== 'en') {
        e.target.value += "族";
      }
      this.cardData.race = e.target.value;
    },
    togithub() {
      window.open("https://gitee.com/ymssx/cardjs");
    },
  },
  created() {
    let lang = localStorage.getItem("lang");
    if (lang) {
      this.lang = lang;
    }
  },
};
</script>

